   .footer {
    background-color: var(--clr-dark);
    padding: 2.5rem;
    text-align: center;
    color: white;
  }
  .footer a {
      text-decoration: none;
      margin:0;
      padding:0;
      color: white;
      font-size: 2rem;
      
  }


  @media (min-width: 64em) {
    .footer .container {
      display: grid;
      grid-template-columns: repeat(3, 1fr), 2fr;
      grid-template-rows: repeat(2, 1fr);
      grid-template-areas: "logo links1  links2 cta" "social links1 links2 copyright";
      gap: 1rem;
      justify-items: start;
    }
  }


  .footer h3 {
    font-family: var(--ff-primary);
    font-size: 3rem;
    line-height: 2rem;
    color: white;
    text-shadow: 0px 0px 3px var(--clr-accent);
  }
 

 
  @media (min-width: 64em) {
    .footer__logo {
      grid-area: logo;
    }
  }
  @media (max-width: 63.9em) {
    .footer__social {
      margin-bottom: 1.875rem;
    }
  }
  @media (max-width: 87.45em) {
    .footer__social {
      grid-area: social;
      align-self: top;
    }
  }


  .footer__social a {
    height: 1rem;
    border: 1px solid white;
    border-radius: 30px;
    color: white;
    font-size: 1rem;
    padding: 0.75rem 0.5rem;
  }

  .footer__social a:hover {
    border: 1px solid teal;
    color: teal;
  }

  .footer__social i {
   margin-right: 0.75rem;
  }


  .footer__social a:not(:last-child) {
    margin-right: 1rem;
  }
  


  @media (min-width: 64em) {
    .footer__links {
      align-items: middle;
    }
  
    .footer__links.col1 {
      grid-area: links1;
      margin: 1rem;
    }
  

  .footer__links.col2 {
      grid-area: links2;
      margin: 1rem;
    }

    .footer__links a{
    
      color: var(--clr-accent);
        display: flex;
        flex-direction: column;
        align-items:flex-start;
        font-size: 2rem;
        font-family: var(--ff-secondary);
       
      }
  }


  .footer__links a{
    -webkit-text-stroke-width: 1px;
    -webkit-text-stroke-color: white;
    color: var(--clr-dark);
      display: flex;
      flex-direction: column;
      align-items: center;
      font-size: 1.25rem;
      font-family: var(--ff-secondary);
    }
  
  .footer__links a:hover {
    color: white;
    text-shadow: 0px 0px 5px var(--clr-accent);
  }
