  .footer__copyright {
    font-size: 0.8125rem;
    color: #b0c4de;
    margin-top: 4rem;
  }
  @media (min-width: 64em) {
    .footer__copyright {
      grid-area: copyright;
      align-self: middle;
      justify-self: end;
    }
  }
  .footer .attribution a {
    font-size: 1rem;
    color:hotpink;
    font-weight: bolder;
    margin-top:1rem;
  }