.about{
   width:100%;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   margin-top: 8rem;
   background: var(--clr-dark);
   color: var(--clr-light);
   padding-bottom: 8rem;
  
}
.about-text{
   display: flex;
   width:100%;
   flex-direction: column;
   display: flex;
   color: white;
   padding: 1rem;
   margin-top:2rem;
}

.highlight-text{
color: var(--clr-accent);
font-family: var(--ff-secondary);
font-weight: 900;
font-size: larger;
margin-top: 1rem;
position: relative;
padding-left: 1rem;
}



.about-image{
   display: flex;
   width:100%;
   flex-direction: column;
   display: flex;
   color: white;
   padding: 1rem;
}

.highlight-text:before{
        content: "";
        display: block;
        width: 2.5px;
        height: 4.5rem;
        background: var(--clr-accent);
        left: 0;
        top: 0%;
        position: absolute;
}





.about-image h1 {
   margin-bottom: 4rem;
   font-family: Audiowide, sans-serif;
   color: var(--clr-dark);
   -webkit-text-stroke-width: 1px;
   -webkit-text-stroke-color: var(--clr-accent);
   font-weight: 400;
   text-transform: uppercase;
   display: block;
   font-size: 3em;
   
}

.text-body {
  margin-top:0;
  padding: 0;
  letter-spacing: 0.6px;
  font-size: 0.5rem;
  font-weight: 300;
  font-family:var(--ff-tertiary);
color: white;
}



.about-btn{
  display: flex;
  justify-content: center;
   align-items: center;
   font-size: 0.8em;
   background: transparent;
   /* background: rgb(233,0,179);
background: radial-gradient(circle, rgba(233,0,179,1) 0%, rgba(0,153,202,1) 100%); */
   shadow: var(--bs);
   border-radius: 5px;
   border: 1px solid white;
    color:var(--clr-accent);
   text-decoration: none;
   padding: 1em 3em;
   font-weight: var(--fw-bold);
   font-family: var(--ff-primary);
   margin: 1rem;
   margin-bottom: 6rem;
}

.about-btn:hover{
  background: rgb(0,153,202);
background: radial-gradient(circle, rgba(0,153,202,1) 0%, rgba(233,0,179,1) 100%);
   transition: all ease 0.5s;
   color:white;
 }




@media(min-width:800px){
 .about{
   width:100%;
   height: 100vh;
   box-sizing: border-box;
   display: flex;
   flex-direction: row;
   align-items: center;
   justify-content: space-between;
   padding-top: 8rem;
 }

 .text-highlight:before{
  content: "";
  display: block;
  width: 2.5px;
  height: 80px;
  background: var(--clr-accent);
  left: 0;
  top: 0%;
  position: absolute;
}

 .about-text{
   display: flex;
   width: 60%;
   height: 100%;
   flex-direction: column;
  margin-top: 1rem;
 }
   

.about-image{
   width: 70%;
   height: 120vh;
  margin-top:6rem;
}

p{
   font-size: 1rem;
 
 }
 

}


